.App {
  text-align: center;
  display: flex;
  background-color: #343541;
  color: white;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.sidemenu {
  width: 260px;
  padding: 10px;
  /* border: 1px solid white; */
  background-color: #202123;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* .sidemenu-title-container{
  background-image: url('../public/logo2.png');
  background-repeat: no-repeat;
  background-size: contain;
} */

.side-menu-button {
  /* margin-top: 50px; */
  margin-top: 10px;
  padding: 12px;
  border: 1px solid white;
  border-radius: 5px;
  text-align: left;
  transition: ease 0.25s all;
}

.side-menu-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.side-menu-button span {
  padding-left: 6px;
  padding-right: 12px;
}

.models {
  margin-bottom: 10px;
  /* padding: 12px; */
  /* border: 1px solid white; */
  border-radius: 5px;
}

.model-select {
  width: 100%;
  padding: 12px;
  border-radius: 5px;
  text-align: left;
}

.chatbox {
  height: 90vh;
  flex: 1 1;
  /* border: 1px solid white; */
  background-color: #343541;
  position: relative;
  overflow-y: scroll;
}

.chat-input-holder {
  display: block;
  width: 80%;
  padding: 24px;
  position: fixed;
  bottom: 0;
  /* left: 0; */
  /* right: 0; */
  size: 90%;
  margin-bottom: -5px;
  
}

.chat-input-form-holder{
  background-color: #40414f;
  box-shadow: 0 0 8px 0px rgba(0, 0, 0.25);
  border-radius: 5px;
}

form{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.chat-input-textarea {
  background-color: #40414f;
  width: 90%;
  padding: 12px;
  color: white;
  font-size: 1.25em;
  border-radius: 5px;
  border: none;
  outline: none;
}

.chat-input-button{
  border: none;
  background-color: inherit;
}

.chat-log {
  /* padding: 24px; */
  text-align: left;
}

.chat-message.chatgpt {
  background-color: #444654;
  /* align-items: center; */
}

.chat-message-center {
  display: flex;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  padding: 12px;
  padding-left: 24px;
  padding-right: 24px;
}

.message.me::first-letter {
  text-transform: uppercase;
}

.avatar {
  background: white;
  border-radius: 50%;
  padding-left: 1px;
  min-width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.avatar.chatgpt {
  background: #0da37f;
  border-radius: 50%;
  min-width: 40px;
  height: 40px;
}

.message {
  padding-left: 40px;
  padding-right: 40px;
  text-align: justify;
  white-space: pre-line;
}

.message.chatgpt::first-line{
  
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}